#app {
  font-family: 'Gill Sans W04 Book', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 19px;
  letter-spacing: 0.005em;
}
select,
option {
  font-family: 'Gill Sans W04 Book', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
select:focus,
a:focus {
  outline: 1px dashed #ccc;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Gill Sans W01 Bold', Helvetica, Arial, sans-serif;
}
em {
  font-family: 'GillSansW01-MediumItali', Helvetica, Arial, sans-serif;
  font-style: normal;
  letter-spacing: -0.01em;
}
h1 {
  margin-bottom: 0;
}
.subtitle {
  color: #d86f5a;
  margin-top: 0;
}
a,
a:visited,
a:active {
  color: #d86f5a;
  text-decoration: none;
}

/* -- grid -- */
body {
  margin: 40px;
}
main {
  width: 90%;
  display: grid;
  grid-gap: 40px;
  grid-template-columns: 1fr 1fr;
  grid-auto-columns: minmax(500px, auto);
}
@media screen and (max-width: 850px) {
main {
    grid-template-columns: 1fr;
}
}
.usage {
  margin: 35px 0 0;
}


































































































